<template>

<div class="slider">
  <img v-for="(img, index) in images" class="sliderImages" :src="img.url" :alt="img.alt" v-bind:key="index">
</div>

</template>

<script>

export default {
  name: 'SliderOne',
  props: [
    "images"
  ],
  mounted() {
    /* eslint-env jquery */
    $(document).ready(() => {
      $('.slider').slick({
        arrows: false,
        autoplay: true,
        autoplaySpeed: 4000,
        dots: false,
        easing: 'ease-in-out',
        pauseOnFocus: 'false',
        pauseOnHover: 'false',
        speed: 2000,
        touchThreshold: 50
      });
    });
  },
}

</script>

<style>

.slick-list {
  height: max-content;
}

</style>

<style scoped>

.slider {
  overflow: hidden;
  background-color: white;
}

.sliderImages {
  height: 100vh;
  object-fit: cover;
}

</style>